import { React, useCallback, useEffect, useRef, useState } from 'react';
import { CSSTransition } from 'react-transition-group';

//icons
import { ReactComponent as CogIcon } from './icons/cog.svg';
import { ReactComponent as ChevronIcon } from './icons/chevron.svg';
import { ReactComponent as ArrowIcon } from './icons/arrow.svg';
import { ReactComponent as BoltIcon } from './icons/bolt.svg';
import DarkLightSwitch from './DarkLightSwitch';

function DropdownMenu() {

    const [activeMenu, setActiveMenu] = useState('main');
    const [menuHeight, setMenuHeight] = useState(null);

    const dropdownRef = useRef();

    useEffect(() => {
        setMenuHeight(dropdownRef.current?.firstChild.offsetHeight);
    }, [])

    const calcHeight = el => {
        const height = el.offsetHeight;
        setMenuHeight(height);
    }

    function DropdownItem(props) {
        let classLeft = props.leftIcon ? "icon-button" : "";
        let mainClass = props.noClick ? "menu-item-no-click" : "menu-item"
        return (
            <div>
                <a href="#" className={mainClass} onClick={() => props.goToMenu && setActiveMenu(props.goToMenu)}>
                    <span className={classLeft}>{props.leftIcon}</span>
                    {props.children}
                    <span className="icon-right">{props.rightIcon}</span>
                </a>
            </div>
        );
    }

    return (
        <div className="dropdown" style={{ height: menuHeight }} ref={dropdownRef}>

            <CSSTransition
                in={activeMenu === 'main'}
                unmountOnExit
                timeout={250}
                classNames="menu-primary"
                onEnter={calcHeight}
            >
                <div className="menu">
                    <DropdownItem><h2>My Profile</h2></DropdownItem>
                    <DropdownItem
                        leftIcon={<CogIcon />}
                        rightIcon={<ChevronIcon />}
                        goToMenu="settings"
                    >Settings</DropdownItem>
                    <DropdownItem noClick="true"><DarkLightSwitch/><h3 id="theme-text">Dark theme</h3></DropdownItem>
                </div>
            </CSSTransition>

            {/*SECOND MENU \/ */}
            <CSSTransition
                in={activeMenu === 'settings'}
                unmountOnExit
                timeout={250}
                classNames="menu-secondary"
                onEnter={calcHeight}
            >
                <div className="menu">
                    <DropdownItem goToMenu="main" leftIcon={<ArrowIcon />} noClick="true">
                        <h2>Settings</h2>
                    </DropdownItem>
                    <DropdownItem leftIcon={<BoltIcon />}>HTML</DropdownItem>
                    <DropdownItem leftIcon={<BoltIcon />}>CSS</DropdownItem>
                    <DropdownItem leftIcon={<BoltIcon />}>JavaScript</DropdownItem>
                    <DropdownItem leftIcon={<BoltIcon />}>Awesome!</DropdownItem>
                </div>
            </CSSTransition>
        </div>
    );
}

export default DropdownMenu;